"use client";

import { MemoryPageSettings } from "@/lib/types/memoryPage";

interface SettingsPanelProps {
  settings: MemoryPageSettings;
  onChange: (settings: MemoryPageSettings) => void;
}

const backgroundColors = [
  { value: '#ffffff', label: 'Beyaz' },
  { value: '#fbfbfd', label: 'Açık Gri' },
  { value: '#f5f5f0', label: 'Krem' },
  { value: '#1d1d1f', label: 'Koyu' },
];

const accentColors = [
  { value: '#0071e3', label: 'Mavi' },
  { value: '#34c759', label: 'Yeşil' },
  { value: '#8b7355', label: 'Kahverengi' },
  { value: '#af52de', label: 'Mor' },
  { value: '#ff3b30', label: 'Kırmızı' },
  { value: '#1d1d1f', label: 'Siyah' },
];

const fontFamilies = [
  { value: 'sans-serif', label: 'Modern (Sans-serif)' },
  { value: 'serif', label: 'Klasik (Serif)' },
];

const layouts = [
  { value: 'grid', label: 'Izgara', description: 'Fotoğraflar ızgara düzeninde' },
  { value: 'timeline', label: 'Zaman Çizelgesi', description: 'Kronolojik sıralama' },
  { value: 'carousel', label: 'Karusel', description: 'Yatay kaydırmalı' },
];

export default function SettingsPanel({ settings, onChange }: SettingsPanelProps) {
  const updateSetting = <K extends keyof MemoryPageSettings>(key: K, value: MemoryPageSettings[K]) => {
    onChange({ ...settings, [key]: value });
  };

  return (
    <div className="rounded-2xl bg-white p-8 shadow-sm">
      <h2 className="mb-2 text-[24px] font-semibold leading-[1.16] tracking-[-0.01em] text-[#1d1d1f]">
        Görünüm Ayarları
      </h2>
      <p className="mb-8 text-[17px] leading-[1.47] text-[#6e6e73]">
        Sayfanızın görünümünü özelleştirin
      </p>

      <div className="space-y-8">
        <div>
          <label className="mb-4 block text-[17px] font-medium text-[#1d1d1f]">
            Arka Plan Rengi
          </label>
          <div className="flex flex-wrap gap-3">
            {backgroundColors.map((color) => (
              <button
                key={color.value}
                onClick={() => updateSetting('backgroundColor', color.value)}
                className={`group flex items-center gap-3 rounded-xl border-2 px-4 py-3 transition-all ${
                  settings.backgroundColor === color.value
                    ? 'border-[#0071e3] bg-[#f5f5f7]'
                    : 'border-[#d2d2d7] hover:border-[#86868b]'
                }`}
              >
                <div
                  className="h-6 w-6 rounded-full border border-[#d2d2d7]"
                  style={{ backgroundColor: color.value }}
                />
                <span className="text-[14px] font-medium text-[#1d1d1f]">{color.label}</span>
              </button>
            ))}
          </div>
        </div>

        <div>
          <label className="mb-4 block text-[17px] font-medium text-[#1d1d1f]">
            Vurgu Rengi
          </label>
          <div className="flex flex-wrap gap-3">
            {accentColors.map((color) => (
              <button
                key={color.value}
                onClick={() => updateSetting('accentColor', color.value)}
                className={`flex h-12 w-12 items-center justify-center rounded-xl border-2 transition-all ${
                  settings.accentColor === color.value
                    ? 'border-[#0071e3]'
                    : 'border-transparent hover:border-[#d2d2d7]'
                }`}
              >
                <div
                  className="h-8 w-8 rounded-full"
                  style={{ backgroundColor: color.value }}
                />
              </button>
            ))}
          </div>
        </div>

        <div>
          <label className="mb-4 block text-[17px] font-medium text-[#1d1d1f]">
            Yazı Tipi
          </label>
          <div className="flex gap-3">
            {fontFamilies.map((font) => (
              <button
                key={font.value}
                onClick={() => updateSetting('fontFamily', font.value)}
                className={`flex-1 rounded-xl border-2 px-4 py-3 text-center transition-all ${
                  settings.fontFamily === font.value
                    ? 'border-[#0071e3] bg-[#f5f5f7]'
                    : 'border-[#d2d2d7] hover:border-[#86868b]'
                }`}
              >
                <span
                  className="text-[17px] font-medium text-[#1d1d1f]"
                  style={{ fontFamily: font.value }}
                >
                  {font.label}
                </span>
              </button>
            ))}
          </div>
        </div>

        <div>
          <label className="mb-4 block text-[17px] font-medium text-[#1d1d1f]">
            Yerleşim Düzeni
          </label>
          <div className="grid gap-3 sm:grid-cols-3">
            {layouts.map((layout) => (
              <button
                key={layout.value}
                onClick={() => updateSetting('layout', layout.value as MemoryPageSettings['layout'])}
                className={`rounded-xl border-2 p-4 text-left transition-all ${
                  settings.layout === layout.value
                    ? 'border-[#0071e3] bg-[#f5f5f7]'
                    : 'border-[#d2d2d7] hover:border-[#86868b]'
                }`}
              >
                <span className="block text-[17px] font-medium text-[#1d1d1f]">{layout.label}</span>
                <span className="mt-1 block text-[14px] text-[#6e6e73]">{layout.description}</span>
              </button>
            ))}
          </div>
        </div>

        <div className="space-y-4 border-t border-[#d2d2d7] pt-8">
          <label className="mb-4 block text-[17px] font-medium text-[#1d1d1f]">
            Görünürlük Seçenekleri
          </label>

          <label className="flex cursor-pointer items-center justify-between rounded-xl border border-[#d2d2d7] p-4 transition-all hover:bg-[#f5f5f7]">
            <div>
              <span className="block text-[17px] font-medium text-[#1d1d1f]">Tarihleri Göster</span>
              <span className="block text-[14px] text-[#6e6e73]">Anılardaki tarihler görünsün</span>
            </div>
            <div className="relative">
              <input
                type="checkbox"
                checked={settings.showDates}
                onChange={(e) => updateSetting('showDates', e.target.checked)}
                className="peer sr-only"
              />
              <div className="h-7 w-12 rounded-full bg-[#d2d2d7] transition-colors peer-checked:bg-[#34c759]" />
              <div className="absolute left-1 top-1 h-5 w-5 rounded-full bg-white shadow transition-transform peer-checked:translate-x-5" />
            </div>
          </label>

          <label className="flex cursor-pointer items-center justify-between rounded-xl border border-[#d2d2d7] p-4 transition-all hover:bg-[#f5f5f7]">
            <div>
              <span className="block text-[17px] font-medium text-[#1d1d1f]">Etiketleri Göster</span>
              <span className="block text-[14px] text-[#6e6e73]">Anılardaki etiketler görünsün</span>
            </div>
            <div className="relative">
              <input
                type="checkbox"
                checked={settings.showTags}
                onChange={(e) => updateSetting('showTags', e.target.checked)}
                className="peer sr-only"
              />
              <div className="h-7 w-12 rounded-full bg-[#d2d2d7] transition-colors peer-checked:bg-[#34c759]" />
              <div className="absolute left-1 top-1 h-5 w-5 rounded-full bg-white shadow transition-transform peer-checked:translate-x-5" />
            </div>
          </label>

          <label className="flex cursor-pointer items-center justify-between rounded-xl border border-[#d2d2d7] p-4 transition-all hover:bg-[#f5f5f7]">
            <div>
              <span className="block text-[17px] font-medium text-[#1d1d1f]">Yorumlara İzin Ver</span>
              <span className="block text-[14px] text-[#6e6e73]">Ziyaretçiler yorum bırakabilsin</span>
            </div>
            <div className="relative">
              <input
                type="checkbox"
                checked={settings.allowComments}
                onChange={(e) => updateSetting('allowComments', e.target.checked)}
                className="peer sr-only"
              />
              <div className="h-7 w-12 rounded-full bg-[#d2d2d7] transition-colors peer-checked:bg-[#34c759]" />
              <div className="absolute left-1 top-1 h-5 w-5 rounded-full bg-white shadow transition-transform peer-checked:translate-x-5" />
            </div>
          </label>
        </div>
      </div>
    </div>
  );
}
